@import 'styles/shared.module.css';
.TodoEditor {
  width: 600px;
  display: flex;
  flex-direction: column;
  padding: 16px;
  border: 1px solid #000;
  border-radius: 4px;
  margin-bottom: 20px;
}

.TodoEditor__textarea {
  --line-height: 16px;

  height: calc(var(--line-height) * 3 + 24px);
  padding: 12px 16px;
  border: 1px solid rgba(33, 33, 33, 0.2);
  border-radius: 4px;

  font: inherit;
  line-height: var(--line-height);
  letter-spacing: 0.01em;

  resize: none;
  margin-bottom: 12px;
}

.TodoEditor__button {
  width: 100%;
  margin-left: auto;
  border-radius: 4px;
  padding: 12px 24px;
  background-color: #4b0082;
  color: #fff;
  font-weight: 500;
}

.TodoEditor__button:hover,
.TodoEditor__button:focus {
  background-color: #5e2fa2;
}

.TodoEditor__button:active {
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
}
